.movie__detail {
    margin: 15%;
    display: flex;
    background-color: white;
    margin-top: 100px;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__detail img {
    position: relative;
    top: -50px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__info {
    padding-left: 30px;
    font-weight: 300;
}

.movie__info {
    color: #9c9c9c;
}

.movie__info .movie_title {
    color: #000;
}

.movie__info .movie_title,
.movie__info .movie_year {
    font-weight: 300;
}

.movie_genres {
    display: inline;
}

.genres_genre {
    padding: 0;
    padding-right: .5rem;
    font-size: 16px;
}

@media only screen and (max-width: 1024px) {
    .movie__detail {
        display: block;
        text-align: center;
    }
    .movie__info {
        padding: 0;
    }
    .movie_title {
        margin-top: 10px;
    }
}